// =============================================================================
// SQLITE ADAPTER
// =============================================================================
// SQLite implementation of DatabaseAdapter
// Wraps existing better-sqlite3 code for adapter pattern

import Database from 'better-sqlite3';
import path from 'path';
import fs from 'fs';
import type { DatabaseAdapter } from './base-adapter';
import type { MeteredEndpoint, Payment, UsageLog } from '../metered-endpoints';

// Re-export the schema initialization from metered-endpoints
function initializeSchema(db: Database.Database) {
  // Endpoints table
  db.exec(`
    CREATE TABLE IF NOT EXISTS endpoints (
      id TEXT PRIMARY KEY,
      name TEXT NOT NULL,
      description TEXT,
      upstream_url TEXT NOT NULL,
      price TEXT NOT NULL,
      network TEXT NOT NULL DEFAULT 'base',
      token_address TEXT NOT NULL,
      token_symbol TEXT NOT NULL DEFAULT 'USDC',
      chain_id INTEGER NOT NULL DEFAULT 8453,
      recipient_address TEXT,
      status TEXT NOT NULL DEFAULT 'active',
      created_at TEXT NOT NULL,
      updated_at TEXT NOT NULL,
      created_by TEXT
    )
  `);

  // Migration: Add recipient_address column if it doesn't exist
  try {
    const tableInfo = db.prepare("PRAGMA table_info(endpoints)").all() as Array<{ name: string }>;
    const hasRecipientAddress = tableInfo.some(col => col.name === 'recipient_address');
    
    if (!hasRecipientAddress) {
      db.exec(`ALTER TABLE endpoints ADD COLUMN recipient_address TEXT`);
      console.log('[db] Added recipient_address column');
    }
  } catch (error: any) {
    if (!error.message?.includes('duplicate column name') && !error.message?.includes('no such column')) {
      console.warn('[db] Migration warning:', error.message);
    }
  }

  // Payments table
  db.exec(`
    CREATE TABLE IF NOT EXISTS payments (
      id TEXT PRIMARY KEY,
      endpoint_id TEXT NOT NULL,
      tx_hash TEXT NOT NULL UNIQUE,
      from_address TEXT NOT NULL,
      to_address TEXT NOT NULL,
      amount TEXT NOT NULL,
      token_address TEXT NOT NULL,
      network TEXT NOT NULL,
      verified_at TEXT NOT NULL,
      kyt_status TEXT,
      ofac_status TEXT,
      facilitator TEXT NOT NULL DEFAULT 'cdp',
      FOREIGN KEY (endpoint_id) REFERENCES endpoints(id)
    )
  `);

  // Usage logs table
  db.exec(`
    CREATE TABLE IF NOT EXISTS usage_logs (
      id TEXT PRIMARY KEY,
      endpoint_id TEXT NOT NULL,
      payment_id TEXT,
      timestamp TEXT NOT NULL,
      method TEXT NOT NULL,
      path TEXT NOT NULL,
      status_code INTEGER NOT NULL,
      response_time INTEGER NOT NULL,
      units INTEGER NOT NULL DEFAULT 1,
      ip_address TEXT,
      user_agent TEXT,
      FOREIGN KEY (endpoint_id) REFERENCES endpoints(id),
      FOREIGN KEY (payment_id) REFERENCES payments(id)
    )
  `);

  // API keys table
  db.exec(`
    CREATE TABLE IF NOT EXISTS api_keys (
      id TEXT PRIMARY KEY,
      key_hash TEXT UNIQUE NOT NULL,
      name TEXT NOT NULL,
      role TEXT NOT NULL DEFAULT 'user' CHECK (role IN ('admin', 'user', 'read-only')),
      user_id TEXT,
      rate_limit INTEGER DEFAULT 1000,
      last_used_at TEXT,
      last_used_ip TEXT,
      expires_at TEXT,
      x402_demo_calls_used INTEGER DEFAULT 0,
      x402_demo_calls_limit INTEGER DEFAULT 200,
      x402_demo_amount_used TEXT DEFAULT '0',
      x402_demo_amount_limit TEXT DEFAULT '1000000',
      created_at TEXT NOT NULL,
      updated_at TEXT NOT NULL,
      revoked_at TEXT
    )
  `);

  // Indexes
  db.exec(`
    CREATE INDEX IF NOT EXISTS idx_payments_endpoint_id ON payments(endpoint_id);
    CREATE INDEX IF NOT EXISTS idx_payments_tx_hash ON payments(tx_hash);
    CREATE INDEX IF NOT EXISTS idx_payments_verified_at ON payments(verified_at);
    CREATE INDEX IF NOT EXISTS idx_usage_logs_endpoint_id ON usage_logs(endpoint_id);
    CREATE INDEX IF NOT EXISTS idx_usage_logs_timestamp ON usage_logs(timestamp);
    CREATE INDEX IF NOT EXISTS idx_usage_logs_payment_id ON usage_logs(payment_id);
    CREATE INDEX IF NOT EXISTS idx_endpoints_status ON endpoints(status);
    CREATE UNIQUE INDEX IF NOT EXISTS idx_api_keys_key_hash ON api_keys(key_hash);
    CREATE INDEX IF NOT EXISTS idx_api_keys_user_id ON api_keys(user_id);
    CREATE INDEX IF NOT EXISTS idx_api_keys_role ON api_keys(role);
    CREATE INDEX IF NOT EXISTS idx_api_keys_revoked_at ON api_keys(revoked_at);
  `);

  // Plans table - subscription plans
  db.exec(`
    CREATE TABLE IF NOT EXISTS plans (
      id TEXT PRIMARY KEY,
      name TEXT NOT NULL UNIQUE,
      description TEXT,
      daily_limit INTEGER NOT NULL DEFAULT 1000,
      monthly_limit INTEGER NOT NULL DEFAULT 10000,
      price_cents INTEGER NOT NULL DEFAULT 0,
      features TEXT,
      created_at TEXT NOT NULL DEFAULT (datetime('now')),
      updated_at TEXT NOT NULL DEFAULT (datetime('now'))
    )
  `);

  // Accounts table - user accounts
  db.exec(`
    CREATE TABLE IF NOT EXISTS accounts (
      id TEXT PRIMARY KEY,
      email TEXT NOT NULL UNIQUE,
      password_hash TEXT NOT NULL,
      name TEXT,
      status TEXT NOT NULL DEFAULT 'active' CHECK (status IN ('active', 'suspended', 'deleted')),
      email_verified INTEGER NOT NULL DEFAULT 0,
      created_at TEXT NOT NULL DEFAULT (datetime('now')),
      updated_at TEXT NOT NULL DEFAULT (datetime('now'))
    )
  `);

  // Account plans table - links accounts to their current plan
  db.exec(`
    CREATE TABLE IF NOT EXISTS account_plans (
      id TEXT PRIMARY KEY DEFAULT (lower(hex(randomblob(16)))),
      account_id TEXT NOT NULL REFERENCES accounts(id),
      plan_id TEXT NOT NULL REFERENCES plans(id),
      daily_limit INTEGER NOT NULL DEFAULT 1000,
      monthly_limit INTEGER NOT NULL DEFAULT 10000,
      started_at TEXT NOT NULL DEFAULT (datetime('now')),
      ends_at TEXT,
      is_current INTEGER NOT NULL DEFAULT 1,
      created_at TEXT NOT NULL DEFAULT (datetime('now'))
    )
  `);

  // Account indexes
  db.exec(`
    CREATE INDEX IF NOT EXISTS idx_accounts_email ON accounts(email);
    CREATE INDEX IF NOT EXISTS idx_accounts_status ON accounts(status);
    CREATE INDEX IF NOT EXISTS idx_account_plans_account_id ON account_plans(account_id);
  `);

  // Seed default plans if empty
  const planCount = db.prepare('SELECT COUNT(*) as count FROM plans').get() as { count: number };
  if (planCount.count === 0) {
    db.exec(`
      INSERT INTO plans (id, name, description, daily_limit, monthly_limit, price_cents, features) VALUES
      ('free', 'Free', 'Free tier for getting started', 1000, 10000, 0, '["1,000 API calls/day","10,000 API calls/month","Basic analytics","Community support"]'),
      ('starter', 'Starter', 'For small projects', 10000, 100000, 2900, '["10,000 API calls/day","100,000 API calls/month","Full analytics","Email support","Webhook notifications"]'),
      ('pro', 'Pro', 'For growing businesses', 100000, 1000000, 9900, '["100,000 API calls/day","1,000,000 API calls/month","Advanced analytics","Priority support","Custom webhooks","API key management"]'),
      ('enterprise', 'Enterprise', 'Custom solutions', 999999999, 999999999, 0, '["Unlimited API calls","Custom SLAs","Dedicated support","Custom integrations","On-premise deployment"]')
    `);
  }

  // Cron job runs table - tracks execution of scheduled jobs
  db.exec(`
    CREATE TABLE IF NOT EXISTS cron_job_runs (
      id TEXT PRIMARY KEY,
      job_id TEXT NOT NULL,
      status TEXT NOT NULL DEFAULT 'pending' CHECK (status IN ('pending', 'running', 'completed', 'failed', 'timeout')),
      started_at TEXT NOT NULL DEFAULT (datetime('now')),
      completed_at TEXT,
      duration_ms INTEGER,
      trigger_source TEXT NOT NULL DEFAULT 'cron' CHECK (trigger_source IN ('cron', 'manual', 'retry')),
      attempt_number INTEGER NOT NULL DEFAULT 1,
      input_params TEXT,
      output_summary TEXT,
      error_message TEXT,
      error_stack TEXT,
      metadata TEXT,
      created_at TEXT NOT NULL DEFAULT (datetime('now'))
    )
  `);

  // Cron job config table - configuration for each job
  db.exec(`
    CREATE TABLE IF NOT EXISTS cron_job_config (
      job_id TEXT PRIMARY KEY,
      display_name TEXT NOT NULL,
      description TEXT,
      schedule TEXT,
      enabled INTEGER NOT NULL DEFAULT 1,
      timeout_ms INTEGER NOT NULL DEFAULT 60000,
      max_retries INTEGER NOT NULL DEFAULT 0,
      retry_delay_ms INTEGER NOT NULL DEFAULT 5000,
      alert_on_failure INTEGER NOT NULL DEFAULT 1,
      consecutive_failures INTEGER NOT NULL DEFAULT 0,
      last_success_at TEXT,
      last_failure_at TEXT,
      created_at TEXT NOT NULL DEFAULT (datetime('now')),
      updated_at TEXT NOT NULL DEFAULT (datetime('now'))
    )
  `);

  // Indexes for cron job tables
  db.exec(`
    CREATE INDEX IF NOT EXISTS idx_cron_job_runs_job_id ON cron_job_runs(job_id);
    CREATE INDEX IF NOT EXISTS idx_cron_job_runs_status ON cron_job_runs(status);
    CREATE INDEX IF NOT EXISTS idx_cron_job_runs_started_at ON cron_job_runs(started_at);
    CREATE INDEX IF NOT EXISTS idx_cron_job_runs_job_status ON cron_job_runs(job_id, status);
    CREATE INDEX IF NOT EXISTS idx_cron_job_runs_created_at ON cron_job_runs(created_at);
  `);

  // Seed default job configurations if table is empty
  const configCount = db.prepare('SELECT COUNT(*) as count FROM cron_job_config').get() as { count: number };
  if (configCount.count === 0) {
    db.exec(`
      INSERT INTO cron_job_config (job_id, display_name, description, schedule, timeout_ms, max_retries, alert_on_failure) VALUES
      ('dogfood', 'Dogfood Agent', 'Exercises NexFlow routes end-to-end', '*/5 * * * *', 30000, 0, 1),
      ('scout', 'Scout Agent', 'Tests under-used facilitator routes', '*/15 * * * *', 60000, 0, 1),
      ('coord', 'Coordinator Agent', 'Runs AI optimization loop', '0 */2 * * *', 120000, 1, 1),
      ('recommendation-applier', 'Recommendation Applier', 'Applies agent recommendations', '*/10 * * * *', 60000, 1, 1),
      ('pull-metrics', 'Metrics Puller', 'Fetches external metrics', '0 */4 * * *', 120000, 2, 1),
      ('social-reply', 'Social Reply', 'Processes social reply queue', '*/15 * * * *', 60000, 0, 1),
      ('house-x402-job', 'House x402', 'Internal x402 traffic', '*/10 * * * *', 30000, 0, 0),
      ('crawl-actions', 'Action Crawler', 'Crawls discovery actions', '0 */6 * * *', 300000, 1, 1),
      ('x402-discovery', 'x402 Discovery', 'Discovers new x402 resources', '0 0 * * *', 600000, 1, 1),
      ('facilitator-probes', 'Facilitator Probes', 'Health checks for payment facilitators', '*/5 * * * *', 30000, 0, 1)
    `);
  }
}

export class SqliteAdapter implements DatabaseAdapter {
  private db: Database.Database;

  constructor(dbPath?: string) {
    const DB_PATH = dbPath || path.join(process.cwd(), 'data', 'metered-endpoints.db');
    
    // Ensure data directory exists
    if (!fs.existsSync(path.dirname(DB_PATH))) {
      fs.mkdirSync(path.dirname(DB_PATH), { recursive: true });
    }
    
    this.db = new Database(DB_PATH);
    this.db.pragma('journal_mode = WAL');
    
    // Initialize schema
    initializeSchema(this.db);
  }

  async healthCheck(): Promise<boolean> {
    try {
      this.db.prepare('SELECT 1').get();
      return true;
    } catch {
      return false;
    }
  }

  /**
   * Generic query method for raw SQL
   * SQLite implementation using better-sqlite3
   */
  async query<T = any>(text: string, params?: any[]): Promise<{ rows: T[]; rowCount: number | null }> {
    // Convert PostgreSQL-style $1, $2 placeholders to SQLite-style ?
    let sqliteQuery = text;
    if (params && params.length > 0) {
      sqliteQuery = text.replace(/\$(\d+)/g, () => '?');
    }

    // Determine if this is a SELECT query or a modification query
    const isSelect = /^\s*SELECT/i.test(sqliteQuery.trim());
    
    try {
      if (isSelect) {
        const rows = this.db.prepare(sqliteQuery).all(...(params || [])) as T[];
        return { rows, rowCount: rows.length };
      } else {
        const result = this.db.prepare(sqliteQuery).run(...(params || []));
        return { rows: [] as T[], rowCount: result.changes };
      }
    } catch (error: any) {
      console.error('[SQLite] Query error:', error.message);
      console.error('[SQLite] Original query:', text);
      console.error('[SQLite] Converted query:', sqliteQuery);
      throw error;
    }
  }

  // =============================================================================
  // ENDPOINTS
  // =============================================================================

  async createEndpoint(endpoint: Omit<MeteredEndpoint, 'id' | 'createdAt' | 'updatedAt'> & { id?: string }): Promise<MeteredEndpoint> {
    const id = endpoint.id || `ep_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`;
    const now = new Date().toISOString();

    this.db.prepare(`
      INSERT INTO endpoints (
        id, name, description, upstream_url, price, network, token_address,
        token_symbol, chain_id, recipient_address, status, created_at, updated_at, created_by
      ) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)
    `).run(
      id,
      endpoint.name,
      endpoint.description || null,
      endpoint.upstreamUrl,
      endpoint.price,
      endpoint.network,
      endpoint.tokenAddress,
      endpoint.tokenSymbol,
      endpoint.chainId,
      endpoint.recipientAddress,
      endpoint.status,
      now,
      now,
      endpoint.createdBy || null
    );

    return Promise.resolve({
      ...endpoint,
      id,
      createdAt: now,
      updatedAt: now,
    });
  }

  async getEndpoint(endpointId: string): Promise<MeteredEndpoint | null> {
    const row = this.db.prepare('SELECT * FROM endpoints WHERE id = ? AND status != ?').get(endpointId, 'deleted') as any;
    if (!row) return Promise.resolve(null);
    return Promise.resolve(this.mapRowToEndpoint(row));
  }

  async listEndpoints(filters?: { status?: string; createdBy?: string }): Promise<MeteredEndpoint[]> {
    let query = 'SELECT * FROM endpoints WHERE status != ?';
    const params: any[] = ['deleted'];

    if (filters?.status) {
      query += ' AND status = ?';
      params.push(filters.status);
    }

    if (filters?.createdBy) {
      query += ' AND created_by = ?';
      params.push(filters.createdBy);
    }

    query += ' ORDER BY created_at DESC';

    const rows = this.db.prepare(query).all(...params) as any[];
    return Promise.resolve(rows.map(row => this.mapRowToEndpoint(row)));
  }

  async updateEndpoint(endpointId: string, updates: Partial<MeteredEndpoint>): Promise<MeteredEndpoint | null> {
    const allowedFields = [
      'name', 'description', 'upstreamUrl', 'price', 'network',
      'tokenAddress', 'tokenSymbol', 'chainId', 'recipientAddress', 'status'
    ];

    const setClauses: string[] = [];
    const values: any[] = [];

    for (const [key, value] of Object.entries(updates)) {
      if (allowedFields.includes(key) && value !== undefined) {
        const dbKey = key === 'upstreamUrl' ? 'upstream_url' :
                     key === 'tokenAddress' ? 'token_address' :
                     key === 'tokenSymbol' ? 'token_symbol' :
                     key === 'chainId' ? 'chain_id' :
                     key === 'recipientAddress' ? 'recipient_address' :
                     key === 'createdAt' ? 'created_at' :
                     key === 'updatedAt' ? 'updated_at' :
                     key === 'createdBy' ? 'created_by' : key;
        setClauses.push(`${dbKey} = ?`);
        values.push(value);
      }
    }

    if (setClauses.length === 0) {
      return this.getEndpoint(endpointId);
    }

    setClauses.push('updated_at = ?');
    values.push(new Date().toISOString());
    values.push(endpointId);

    this.db.prepare(`UPDATE endpoints SET ${setClauses.join(', ')} WHERE id = ?`).run(...values);
    return this.getEndpoint(endpointId);
  }

  async deleteEndpoint(endpointId: string): Promise<boolean> {
    const result = this.db.prepare('UPDATE endpoints SET status = ?, updated_at = ? WHERE id = ?').run(
      'deleted',
      new Date().toISOString(),
      endpointId
    );
    return Promise.resolve(result.changes > 0);
  }

  // =============================================================================
  // PAYMENTS
  // =============================================================================

  async createPayment(payment: Omit<Payment, 'id' | 'verifiedAt'>): Promise<Payment> {
    const id = `pay_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`;
    const verifiedAt = new Date().toISOString();

    this.db.prepare(`
      INSERT INTO payments (
        id, endpoint_id, tx_hash, from_address, to_address, amount,
        token_address, network, verified_at, kyt_status, ofac_status, facilitator
      ) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)
    `).run(
      id,
      payment.endpointId,
      payment.txHash,
      payment.fromAddress,
      payment.toAddress,
      payment.amount,
      payment.tokenAddress,
      payment.network,
      verifiedAt,
      payment.kytStatus || null,
      payment.ofacStatus || null,
      payment.facilitator
    );

    return Promise.resolve({
      ...payment,
      id,
      verifiedAt,
    });
  }

  async getPayment(paymentId: string): Promise<Payment | null> {
    const row = this.db.prepare('SELECT * FROM payments WHERE id = ?').get(paymentId) as any;
    if (!row) return Promise.resolve(null);
    return Promise.resolve(this.mapRowToPayment(row));
  }

  async getPaymentByTxHash(txHash: string): Promise<Payment | null> {
    const row = this.db.prepare('SELECT * FROM payments WHERE tx_hash = ?').get(txHash) as any;
    if (!row) return Promise.resolve(null);
    return Promise.resolve(this.mapRowToPayment(row));
  }

  async listPayments(filters?: { endpointId?: string; fromAddress?: string }): Promise<Payment[]> {
    let query = 'SELECT * FROM payments WHERE 1=1';
    const params: any[] = [];

    if (filters?.endpointId) {
      query += ' AND endpoint_id = ?';
      params.push(filters.endpointId);
    }

    if (filters?.fromAddress) {
      query += ' AND from_address = ?';
      params.push(filters.fromAddress);
    }

    query += ' ORDER BY verified_at DESC';

    const rows = this.db.prepare(query).all(...params) as any[];
    return Promise.resolve(rows.map(row => this.mapRowToPayment(row)));
  }

  async getPaymentsForEndpoint(endpointId: string, limit: number = 100): Promise<Payment[]> {
    const rows = this.db.prepare(
      'SELECT * FROM payments WHERE endpoint_id = ? ORDER BY verified_at DESC LIMIT ?'
    ).all(endpointId, limit) as any[];
    return Promise.resolve(rows.map(row => this.mapRowToPayment(row)));
  }

  // =============================================================================
  // USAGE LOGS
  // =============================================================================

  async createUsageLog(log: Omit<UsageLog, 'id' | 'timestamp'>): Promise<UsageLog> {
    const id = `log_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`;
    const timestamp = new Date().toISOString();

    // Add tracing columns if they don't exist (migration support)
    try {
      this.db.exec(`
        ALTER TABLE usage_logs ADD COLUMN conversation_id TEXT;
        ALTER TABLE usage_logs ADD COLUMN agent_id TEXT;
        ALTER TABLE usage_logs ADD COLUMN x402_tx_hash TEXT;
        ALTER TABLE usage_logs ADD COLUMN failure_code TEXT;
      `);
    } catch (e: any) {
      // Columns may already exist, ignore error
      if (!e.message?.includes('duplicate column')) {
        console.warn('[SQLite] Migration warning:', e.message);
      }
    }

    try {
      this.db.prepare(`
        INSERT INTO usage_logs (
          id, endpoint_id, payment_id, timestamp, method, path,
          status_code, response_time, units, ip_address, user_agent,
          conversation_id, agent_id, x402_tx_hash, failure_code
        ) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)
      `).run(
        id,
        log.endpointId,
        log.paymentId || null,
        timestamp,
        log.method,
        log.path,
        log.statusCode,
        log.responseTime,
        log.units,
        log.ipAddress || null,
        log.userAgent || null,
        log.conversationId || null,
        log.agentId || null,
        log.x402TxHash || null,
        log.failureCode || null
      );
    } catch (dbError: any) {
      // Log full error details for debugging
      const errorInfo = {
        message: dbError.message || String(dbError),
        code: dbError.code || 'unknown',
        errno: dbError.errno,
        sql: dbError.sql,
        endpointId: log.endpointId,
        table: 'usage_logs',
      };
      
      console.error('[SQLite] Insert error details:', JSON.stringify(errorInfo, null, 2));
      
      // Re-throw with more context
      throw new Error(
        `SQLite insert failed: ${dbError.message || String(dbError)}. ` +
        `Table: usage_logs, EndpointId: ${log.endpointId}, ` +
        `Error code: ${dbError.code || 'unknown'}, ` +
        `SQL: ${dbError.sql || 'N/A'}`
      );
    }

    return Promise.resolve({
      ...log,
      id,
      timestamp,
    });
  }

  async listUsageLogs(filters?: { endpointId?: string; paymentId?: string }): Promise<UsageLog[]> {
    let query = 'SELECT * FROM usage_logs WHERE 1=1';
    const params: any[] = [];

    if (filters?.endpointId) {
      query += ' AND endpoint_id = ?';
      params.push(filters.endpointId);
    }

    if (filters?.paymentId) {
      query += ' AND payment_id = ?';
      params.push(filters.paymentId);
    }

    query += ' ORDER BY timestamp DESC LIMIT 1000';

    const rows = this.db.prepare(query).all(...params) as any[];
    return Promise.resolve(rows.map(row => this.mapRowToUsageLog(row)));
  }

  async getUsageStats(endpointId: string, startDate?: string, endDate?: string): Promise<{
    totalRequests: number;
    totalPayments: number;
    totalRevenue: string;
    averageResponseTime: number;
  }> {
    let query = `
      SELECT
        COUNT(*) as total_requests,
        COUNT(DISTINCT payment_id) as total_payments,
        COALESCE(SUM(CAST(p.amount AS INTEGER)), 0) as total_revenue,
        COALESCE(AVG(response_time), 0) as avg_response_time
      FROM usage_logs ul
      LEFT JOIN payments p ON ul.payment_id = p.id
      WHERE ul.endpoint_id = ?
    `;
    const params: any[] = [endpointId];

    if (startDate) {
      query += ' AND ul.timestamp >= ?';
      params.push(startDate);
    }

    if (endDate) {
      query += ' AND ul.timestamp <= ?';
      params.push(endDate);
    }

    const row = this.db.prepare(query).get(...params) as any;

    return Promise.resolve({
      totalRequests: row.total_requests || 0,
      totalPayments: row.total_payments || 0,
      totalRevenue: row.total_revenue?.toString() || '0',
      averageResponseTime: row.avg_response_time || 0,
    });
  }

  // =============================================================================
  // HELPERS
  // =============================================================================

  private mapRowToEndpoint(row: any): MeteredEndpoint {
    return {
      id: row.id,
      name: row.name,
      description: row.description,
      upstreamUrl: row.upstream_url,
      price: row.price,
      network: row.network,
      tokenAddress: row.token_address,
      tokenSymbol: row.token_symbol,
      chainId: row.chain_id,
      recipientAddress: row.recipient_address,
      status: row.status,
      createdAt: row.created_at,
      updatedAt: row.updated_at,
      createdBy: row.created_by,
    };
  }

  private mapRowToPayment(row: any): Payment {
    return {
      id: row.id,
      endpointId: row.endpoint_id,
      txHash: row.tx_hash,
      fromAddress: row.from_address,
      toAddress: row.to_address,
      amount: row.amount,
      tokenAddress: row.token_address,
      network: row.network,
      verifiedAt: row.verified_at,
      kytStatus: row.kyt_status,
      ofacStatus: row.ofac_status,
      facilitator: row.facilitator,
    };
  }

  private mapRowToUsageLog(row: any): UsageLog {
    return {
      id: row.id,
      endpointId: row.endpoint_id,
      paymentId: row.payment_id,
      timestamp: row.timestamp,
      method: row.method,
      path: row.path,
      statusCode: row.status_code,
      responseTime: row.response_time,
      units: row.units,
      ipAddress: row.ip_address,
      userAgent: row.user_agent,
      conversationId: row.conversation_id,
      agentId: row.agent_id,
      x402TxHash: row.x402_tx_hash,
      failureCode: row.failure_code,
    };
  }

  // =============================================================================
  // API KEYS
  // =============================================================================

  async createApiKey(apiKey: { id: string; keyHash: string; name: string; role: string; userId?: string; rateLimit: number; expiresAt?: string; createdAt: string; updatedAt: string }): Promise<{ id: string; keyHash: string; name: string; role: string; userId?: string; rateLimit: number; lastUsedAt?: string; expiresAt?: string; createdAt: string; updatedAt: string; revokedAt?: string }> {
    this.db.prepare(
      `INSERT INTO api_keys (id, key_hash, name, role, user_id, rate_limit, expires_at, x402_demo_calls_used, x402_demo_calls_limit, x402_demo_amount_used, x402_demo_amount_limit, created_at, updated_at)
       VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)`
    ).run(
      apiKey.id, apiKey.keyHash, apiKey.name, apiKey.role, apiKey.userId || null,
      apiKey.rateLimit, apiKey.expiresAt || null,
      apiKey.x402DemoCallsUsed || 0, apiKey.x402DemoCallsLimit || 200,
      apiKey.x402DemoAmountUsed || '0', apiKey.x402DemoAmountLimit || '1000000',
      apiKey.createdAt, apiKey.updatedAt
    );
    return this.findApiKeyByHash(apiKey.keyHash) as Promise<{ id: string; keyHash: string; name: string; role: string; userId?: string; rateLimit: number; lastUsedAt?: string; expiresAt?: string; createdAt: string; updatedAt: string; revokedAt?: string }>;
  }

  async findApiKeyByHash(keyHash: string): Promise<{ id: string; keyHash: string; name: string; role: string; userId?: string; rateLimit: number; lastUsedAt?: string; expiresAt?: string; createdAt: string; updatedAt: string; revokedAt?: string } | null> {
    const row = this.db.prepare(
      `SELECT * FROM api_keys WHERE key_hash = ? AND revoked_at IS NULL`
    ).get(keyHash) as any;
    if (!row) return null;
    return {
      id: row.id,
      keyHash: row.key_hash,
      name: row.name,
      role: row.role,
      userId: row.user_id,
      rateLimit: row.rate_limit,
      lastUsedAt: row.last_used_at,
      lastUsedIp: row.last_used_ip,
      expiresAt: row.expires_at,
      x402DemoCallsUsed: row.x402_demo_calls_used || 0,
      x402DemoCallsLimit: row.x402_demo_calls_limit || 200,
      x402DemoAmountUsed: row.x402_demo_amount_used || '0',
      x402DemoAmountLimit: row.x402_demo_amount_limit || '1000000',
      createdAt: row.created_at,
      updatedAt: row.updated_at,
      revokedAt: row.revoked_at,
    };
  }

  async updateApiKeyLastUsed(keyId: string, ipAddress?: string): Promise<void> {
    const now = new Date().toISOString();
    this.db.prepare(
      `UPDATE api_keys SET last_used_at = ?, last_used_ip = ?, updated_at = ? WHERE id = ?`
    ).run(now, ipAddress || null, now, keyId);
  }

  async updateApiKeyX402DemoAllowance(keyId: string, callsUsed: number, amountUsed: string): Promise<void> {
    const now = new Date().toISOString();
    this.db.prepare(
      `UPDATE api_keys SET x402_demo_calls_used = ?, x402_demo_amount_used = ?, updated_at = ? WHERE id = ?`
    ).run(callsUsed, amountUsed, now, keyId);
  }

  async updateApiKeyX402DemoLimits(keyId: string, callsLimit?: number, amountLimit?: string): Promise<void> {
    const now = new Date().toISOString();
    if (callsLimit !== undefined && amountLimit !== undefined) {
      this.db.prepare(
        `UPDATE api_keys SET x402_demo_calls_limit = ?, x402_demo_amount_limit = ?, updated_at = ? WHERE id = ?`
      ).run(callsLimit, amountLimit, now, keyId);
    } else if (callsLimit !== undefined) {
      this.db.prepare(
        `UPDATE api_keys SET x402_demo_calls_limit = ?, updated_at = ? WHERE id = ?`
      ).run(callsLimit, now, keyId);
    } else if (amountLimit !== undefined) {
      this.db.prepare(
        `UPDATE api_keys SET x402_demo_amount_limit = ?, updated_at = ? WHERE id = ?`
      ).run(amountLimit, now, keyId);
    }
  }

  async revokeApiKey(keyId: string): Promise<boolean> {
    const now = new Date().toISOString();
    const result = this.db.prepare(
      `UPDATE api_keys SET revoked_at = ?, updated_at = ? WHERE id = ? AND revoked_at IS NULL`
    ).run(now, now, keyId);
    return result.changes > 0;
  }

  async listApiKeys(userId?: string): Promise<{ id: string; keyHash: string; name: string; role: string; userId?: string; rateLimit: number; lastUsedAt?: string; expiresAt?: string; createdAt: string; updatedAt: string; revokedAt?: string }[]> {
    let query = `SELECT * FROM api_keys WHERE revoked_at IS NULL`;
    const params: any[] = [];
    if (userId) {
      query += ` AND user_id = ?`;
      params.push(userId);
    }
    query += ` ORDER BY created_at DESC`;
    const rows = this.db.prepare(query).all(...params) as any[];
    return Promise.resolve(rows.map((row) => ({
      id: row.id,
      keyHash: row.key_hash,
      name: row.name,
      role: row.role,
      userId: row.user_id,
      rateLimit: row.rate_limit,
      lastUsedAt: row.last_used_at,
      lastUsedIp: row.last_used_ip,
      expiresAt: row.expires_at,
      x402DemoCallsUsed: row.x402_demo_calls_used || 0,
      x402DemoCallsLimit: row.x402_demo_calls_limit || 200,
      x402DemoAmountUsed: row.x402_demo_amount_used || '0',
      x402DemoAmountLimit: row.x402_demo_amount_limit || '1000000',
      createdAt: row.created_at,
      updatedAt: row.updated_at,
      revokedAt: row.revoked_at,
    })));
  }

  close(): void {
    this.db.close();
  }
}

