// =============================================================================
// AUTH SIGNUP ROUTE
// =============================================================================
// POST /api/v1/auth/signup - Create a new account with free plan

import { NextRequest, NextResponse } from 'next/server';
import { z } from 'zod';
import { createAccount, findAccountByEmail } from '@/db/accounts';
import { createApiKey } from '@/db/api-keys';

// Input validation schema
const signupSchema = z.object({
  email: z.string().email('Invalid email address'),
  password: z
    .string()
    .min(8, 'Password must be at least 8 characters')
    .max(128, 'Password must be at most 128 characters')
    .regex(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/,
      'Password must contain at least one uppercase letter, one lowercase letter, and one number'
    ),
  name: z.string().min(1).max(100).optional(),
});

export async function POST(request: NextRequest) {
  try {
    // Parse and validate request body
    const body = await request.json();
    const validation = signupSchema.safeParse(body);

    if (!validation.success) {
      return NextResponse.json(
        {
          error: 'Validation failed',
          code: 'VALIDATION_ERROR',
          details: validation.error.issues.map((i) => ({
            field: i.path.join('.'),
            message: i.message,
          })),
        },
        { status: 400 }
      );
    }

    const { email, password, name } = validation.data;

    // Check if email is already registered
    const existingAccount = await findAccountByEmail(email);
    if (existingAccount) {
      return NextResponse.json(
        {
          error: 'Email already registered',
          code: 'EMAIL_EXISTS',
        },
        { status: 409 }
      );
    }

    // Create the account (includes free plan by default)
    const account = await createAccount({ email, password, name });

    // Create an initial API key for the account
    const apiKey = await createApiKey({
      name: 'Default API Key',
      role: 'user',
      userId: account.id,
      rateLimit: 1000,
    });

    // Return success response
    return NextResponse.json(
      {
        success: true,
        accountId: account.id,
        plan: {
          name: account.plan.name,
          dailyLimit: account.plan.dailyLimit,
          monthlyLimit: account.plan.monthlyLimit,
        },
        apiKey: {
          id: apiKey.id,
          name: apiKey.name,
          token: apiKey.token, // Only returned once on creation
        },
        message: 'Account created successfully. Welcome to NexFlow!',
      },
      { status: 201 }
    );
  } catch (error: any) {
    console.error('[auth/signup] Error:', error);

    // Handle specific database errors
    if (error.message?.includes('UNIQUE constraint') || error.code === '23505') {
      return NextResponse.json(
        {
          error: 'Email already registered',
          code: 'EMAIL_EXISTS',
        },
        { status: 409 }
      );
    }

    return NextResponse.json(
      {
        error: 'Failed to create account',
        code: 'INTERNAL_ERROR',
        details: process.env.NODE_ENV === 'development' ? error.message : undefined,
      },
      { status: 500 }
    );
  }
}

// =============================================================================
// OPTIONS - CORS preflight
// =============================================================================
export async function OPTIONS() {
  return new NextResponse(null, {
    status: 204,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'POST, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type',
    },
  });
}

